import {UserModel} from "../../model/user";
const  userModel = new UserModel()
export default {
  data() {
    return {
        invitation_code:'',
        input_invitation_code:''
    };
  },
  methods: {
    toIndex(){
      uni.navigateTo({
        url: "/pages/index/index"
      });
    },
    changeValue(e){
        this.input_invitation_code = e.detail.value;
      },
    invite()
    {
        if(!(/^[A-Za-z0-9]+$/).test(this.input_invitation_code)) {
          uni.showToast({
            title: '邀请码只能包含数字和字母',
            icon: 'none'
          });
        }else {
          let params = {
          invitation_code: this.input_invitation_code
          }
          userModel.activate(params).then((res) => {
            uni.showToast({
              title:res.message,
              icon:'none'
            })
            if (res.code == 201) {
              this.input_invitation_code = ''
              uni.navigateBack()
            }
        })
        }
        
    }
  },
  components: {},
  onLoad: function() {
      let user = uni.getStorageSync('users')
      let invitation_code = user.invitation_code
      this.invitation_code = invitation_code
  },
  onShow: function() {
    uni.hideTabBar();
  },
  onHide: function() {}
};
